'use client';

import React, { useState } from 'react';
import { Station, LineConfig } from './types';
import { INITIAL_STATIONS, INITIAL_CONFIG } from './constants';
import { StripMap } from './components/StripMap';
import { Editor } from './components/Editor';

export default function App() {
  const [stations, setStations] = useState<Station[]>(INITIAL_STATIONS);
  const [config, setConfig] = useState<LineConfig>(INITIAL_CONFIG);

  return (
    <div className="flex h-screen w-full bg-gray-50 text-gray-900 overflow-hidden">
      {/* Left Sidebar: Controls */}
      <div className="w-80 md:w-96 h-full flex-shrink-0 z-20">
        <Editor 
          stations={stations} 
          config={config} 
          onConfigChange={setConfig} 
          onStationsChange={setStations}
        />
      </div>

      {/* Main Content: Preview */}
      <div className="flex-1 h-full flex flex-col relative bg-gray-200 overflow-hidden">
        {/* Header bar */}
        <div className="bg-white border-b border-gray-200 p-4 flex justify-between items-center shadow-sm z-10">
          <div>
             <h1 className="text-xl font-bold text-gray-800">NYC Strip Map Generator</h1>
             <p className="text-xs text-gray-500">Design your own car diagram.</p>
          </div>
          <div className="text-xs text-gray-400">
             Scroll horizontally to see full route
          </div>
        </div>

        {/* Workspace */}
        <div className="flex-1 flex flex-col items-center justify-center p-8 overflow-auto">
          <div className="w-full shadow-2xl rounded-sm overflow-hidden border-4 border-gray-800">
            <StripMap config={config} stations={stations} />
          </div>
          
          <div className="mt-8 text-sm text-gray-500 max-w-lg text-center">
            <p>Use your browser's screenshot tool or print feature to save your map.</p>
            <p className="mt-2 text-xs opacity-60">
              Not affiliated with the MTA. Colors and styles are approximations.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
