import React from 'react';
import { LINE_COLORS } from '../constants';

interface TransferIconProps {
  line: string;
  size?: 'sm' | 'md' | 'lg' | 'xl' | 'xxl';
  className?: string;
}

export const TransferIcon: React.FC<TransferIconProps> = ({ line, size = 'md', className = '' }) => {
  const code = line.toUpperCase();
  const colors = LINE_COLORS[code] || LINE_COLORS['DEFAULT'];

  const sizeClasses = {
    sm: 'w-4 h-4 text-[9px]',
    md: 'w-6 h-6 text-xs',
    lg: 'w-8 h-8 text-sm',
    xl: 'w-16 h-16 text-3xl font-bold',
    xxl: 'w-24 h-24 text-6xl font-bold'
  };

  return (
    <div
      className={`rounded-full flex items-center justify-center font-bold flex-shrink-0 ${sizeClasses[size]} ${className}`}
      style={{ backgroundColor: colors.bg, color: colors.text }}
    >
      {code}
    </div>
  );
};