import React from 'react';
import { Station, LineConfig } from '../types';
import { LINE_COLORS } from '../constants';
import { TransferIcon } from './TransferIcon';
import { ArrowLeft, ArrowRight } from 'lucide-react';

// Inline accessible icon to avoid import issues with specific library versions
const AccessibleIcon = ({ size = 14, className = "" }: { size?: number, className?: string }) => (
  <span
    style={{ fontSize: size, lineHeight: 1 }}
    className={className}
    role="img"
    aria-label="Accessible"
  >
    â™¿
  </span>
);

interface StripMapProps {
  config: LineConfig;
  stations: Station[];
}

const DEFAULT_BOROUGHS = ['BROOKLYN', 'MANHATTAN', 'QUEENS'];

const DiagonalRod: React.FC<{ label: string; bg: string; text: string }> = ({ label, bg, text }) => (
  <div
    className="h-14 min-w-[220px] max-w-[300px] rounded-full border-[3px] border-white px-8 shadow-lg flex items-center justify-center"
    style={{ backgroundColor: bg, transform: 'rotate(-18deg)' }}
    title={label}
  >
    <span
      className="block text-sm font-extrabold tracking-wide whitespace-nowrap text-center max-w-[240px] truncate"
      style={{ color: text, transform: 'rotate(18deg)' }}
    >
      {label}
    </span>
  </div>
);

const BoroughSplit: React.FC = () => (
  <div className="flex flex-col items-center justify-end pb-1">
    <div className="h-7 border-l-2 border-white/90" />
    <div className="h-8 w-8 border-t-2 border-r-2 border-white/90 rotate-45 -mt-1 ml-2" />
  </div>
);

const getStopHighlights = (stations: Station[]) => {
  if (stations.length === 0) {
    return { nextStop: 'NEXT STOP', lastStop: 'LAST STOP', intermediateStops: [] as Station[] };
  }

  const nextStopStation = stations.find((station) => station.isNextStop) ?? stations[0];
  const terminus = [...stations].reverse().find((station) => station.isTerminus);
  const lastStopStation = terminus ?? stations[stations.length - 1];
  const nextStop = nextStopStation.name;
  const lastStop = lastStopStation.name;
  const intermediateStops = stations.filter(
    (station) => station.id !== nextStopStation.id && station.id !== lastStopStation.id
  );

  return { nextStop, lastStop, intermediateStops };
};

export const StripMap: React.FC<StripMapProps> = ({ config, stations }) => {
  const mainLineColor = LINE_COLORS[config.lineCode] || LINE_COLORS['DEFAULT'];
  const { nextStop, lastStop, intermediateStops } = getStopHighlights(stations);
  const destinationText = config.item_destinations?.[0] ?? config.endText;
  const boroughs =
    config.item_boroughs && config.item_boroughs.length > 0
      ? config.item_boroughs.map((borough) => borough.toUpperCase())
      : DEFAULT_BOROUGHS;

  if (config.theme === 'modern-black') {
    return (
      <div className="w-full overflow-x-auto custom-scrollbar bg-black p-8 min-h-[300px] flex items-center justify-center">
        <div className="min-w-[1200px] w-full select-none">
          <div className="grid grid-cols-[240px_1fr] gap-8">
            <div className="flex flex-col items-start">
              <TransferIcon line={config.lineCode} size="xl" />
              <h1 className="mt-4 text-3xl font-bold text-white leading-tight">{config.viaText}</h1>
              <p className="mt-2 text-lg font-semibold text-gray-300">{destinationText}</p>

              <div className="mt-5 space-y-1">
                {boroughs.map((borough) => (
                  <p
                    key={borough}
                    className="text-[11px] font-semibold tracking-[0.25em] text-gray-400"
                  >
                    {borough}
                  </p>
                ))}
              </div>
            </div>

            <div className="pt-12">
              <div className="flex items-end gap-7">
                <div className="flex flex-col items-center gap-2">
                  <span className="text-xs font-bold tracking-[0.25em] text-gray-300">NEXT STOP</span>
                  <DiagonalRod
                    label={nextStop}
                    bg={mainLineColor.bg}
                    text={mainLineColor.text}
                  />
                </div>

                <BoroughSplit />

                <div className="flex-1 min-w-[360px] flex flex-col items-center gap-2">
                  <span className="text-xs font-bold tracking-[0.25em] text-gray-300">
                    CONTINUING STOPS
                  </span>
                  <div className="w-full flex items-center gap-4">
                    <div className="flex items-center gap-2">
                      <span className="w-3 h-3 rounded-full bg-white" />
                      <span className="w-3 h-3 rounded-full bg-white" />
                    </div>
                    <div className="h-[2px] flex-1 bg-white/70" />
                    <div className="flex items-center gap-2">
                      <span className="w-2 h-2 rounded-full bg-white/75" />
                      <span className="w-2 h-2 rounded-full bg-white/75" />
                      <span className="w-2 h-2 rounded-full bg-white/75" />
                    </div>
                  </div>
                  <div className="w-full mt-2 flex flex-wrap gap-x-3 gap-y-1 justify-center text-sm text-gray-200">
                    {intermediateStops.map((station) => (
                      <div key={station.id} className="flex items-center gap-2">
                        <span className="w-1.5 h-1.5 rounded-full bg-white/80" />
                        <span>{station.name}</span>
                      </div>
                    ))}
                    {intermediateStops.length === 0 && (
                      <span className="text-[10px] tracking-[0.2em] text-gray-400">
                        NO INTERMEDIATE STOPS
                      </span>
                    )}
                  </div>
                </div>

                <div className="flex flex-col items-center gap-2">
                  <span className="text-xs font-bold tracking-[0.25em] text-gray-300">LAST STOP</span>
                  <DiagonalRod
                    label={lastStop}
                    bg={mainLineColor.bg}
                    text={mainLineColor.text}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  const themeStyles = {
    'vintage-white': {
      bg: 'bg-gray-100',
      text: 'text-slate-900',
      line: mainLineColor.bg, // Usually standard red or black in old maps, but let's keep line color
      subText: 'text-slate-600',
      dotBorder: 'border-gray-800',
      arrowColor: 'text-slate-800'
    },
    'digital-find': {
      bg: 'bg-[#1a1a1a]',
      text: 'text-[#fbbf24]', // Amber text often used in displays
      line: 'bg-yellow-500', // Digital representation often simplistic
      subText: 'text-blue-300',
      dotBorder: 'border-white',
      arrowColor: 'text-green-500'
    }
  };

  const currentTheme =
    themeStyles[config.theme as keyof typeof themeStyles] || themeStyles['vintage-white'];
  const isDigital = config.theme === 'digital-find';

  return (
    <div className={`w-full overflow-x-auto custom-scrollbar ${currentTheme.bg} p-8 min-h-[300px] flex items-center justify-center`}>
      <div className="min-w-[1000px] w-full relative select-none">

        {/* Header Information (Left Side) - R160/Modern Style */}
        <div className="absolute left-0 top-0 flex flex-col max-w-[340px] z-20">
          <div className="flex flex-col gap-1">
            <div className="flex items-start gap-4">
              <TransferIcon line={config.lineCode} size="xxl" className="shrink-0" />
              <div className="flex flex-col pt-1">
                <h1 className={`text-4xl font-bold tracking-tight ${currentTheme.text} leading-none mb-1`}>
                  {config.viaText}
                </h1>
                {config.item_destinations && config.item_destinations.length > 0 && (
                  <div className={`text-3xl font-bold tracking-tight ${currentTheme.text} leading-tight`}>
                    {config.item_destinations.map((dest, i) => (
                      <div key={i}>{dest}</div>
                    ))}
                  </div>
                )}
              </div>
            </div>

            {config.item_boroughs && config.item_boroughs.length > 0 && (
              <div className={`mt-4 text-sm font-bold uppercase tracking-widest ${currentTheme.text} space-y-0.5 ml-1`}>
                {config.item_boroughs.map((borough, i) => (
                  <div key={i}>{borough}</div>
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Directional Arrow Left */}
        <div className={`absolute left-0 top-32 mt-8 flex items-center gap-2 ${currentTheme.arrowColor} opacity-50 z-10`}>
          <ArrowLeft size={48} strokeWidth={3} />
        </div>

        {/* Directional Arrow Right (Far Right) */}
        <div className={`absolute right-0 top-32 flex items-center gap-2 ${currentTheme.arrowColor} opacity-50 z-10`}>
          <ArrowRight size={48} strokeWidth={3} />
        </div>

        {/* The Main Strip Line */}
        <div className="relative mt-32 ml-[360px] mr-[100px]">
          {/* The physical line background */}
          <div
            className="absolute top-[9px] left-0 w-full h-3 z-0"
            style={{ backgroundColor: currentTheme.line }}
          />

          {/* Stations Container */}
          <div className="flex justify-between items-start relative z-10 w-full px-4">
            {stations.map((station, index) => {
              const isNextStop = station.isNextStop;
              const isTerminus = station.isTerminus;
              const showRod = isNextStop || isTerminus;

              // Check for borough change
              const prevStation = stations[index - 1];
              const isBoroughBoundary = prevStation && station.borough && prevStation.borough !== station.borough;

              return (
                <div key={station.id} className="flex flex-col items-center relative group" style={{ width: `${100 / stations.length}%` }}>

                  {/* Borough Separator Line */}
                  {isBoroughBoundary && (
                    <div className="absolute top-[9px] -left-1/2 w-full h-10 pointer-events-none z-0">
                      {/* This is a visual trick to create a "cut" or separator. 
                         We place a diagonal white line over the main strip line. */}
                      <div className="absolute left-0 -ml-1 top-[-10px] w-2 h-12 bg-gray-200 rotate-[30deg] z-10 border-l-2 border-r-2 border-gray-900" />
                    </div>
                  )}

                  {/* Dot and Name Container */}
                  <div className="relative flex flex-col items-center mb-2">

                    {/* NEXT/LAST STOP Label */}
                    {showRod && (
                      <div className={`absolute -top-6 whitespace-nowrap text-xs font-bold tracking-wider ${isNextStop ? 'text-blue-400 animate-blink' : currentTheme.text}`}>
                        {isNextStop ? 'NEXT STOP' : 'LAST STOP'}
                      </div>
                    )}

                    {/* Station Dot / Rod */}
                    <div
                      className={`
                      relative z-10 transition-all duration-200
                      ${showRod ? 'w-16 h-8 rounded-full' : 'w-8 h-8 rounded-full'}
                      border-[3px] ${currentTheme.dotBorder}
                    `}
                      style={{
                        backgroundColor: isDigital ? (isTerminus ? '#ef4444' : (isNextStop ? '#3b82f6' : '#22c55e')) : (isTerminus ? currentTheme.line : (config.theme === 'vintage-white' ? '#fff' : '#000'))
                      }}
                    />

                    {/* Station Name (Rotated) */}
                    <div className={`absolute bottom-9 left-1/2 w-[300px] origin-bottom-left -rotate-45 ${showRod ? 'translate-x-6' : 'translate-x-2'} pointer-events-none z-20`}>
                      <div className={`text-lg font-bold leading-tight ${currentTheme.text} flex items-center gap-2`}>
                        {station.name}
                        {station.isAccessible && (
                          <AccessibleIcon size={16} className={isDigital ? 'text-blue-400' : 'text-blue-500'} />
                        )}
                      </div>
                    </div>
                  </div>

                  {/* Transfers */}
                  <div className="mt-2 flex flex-col items-center gap-1 min-h-[80px]">
                    {station.transfers.length > 0 && (
                      <div className="flex flex-wrap justify-center gap-1 max-w-[80px]">
                        {station.transfers.map((t, i) => (
                          <TransferIcon key={i} line={t} size="sm" />
                        ))}
                      </div>
                    )}
                    {isDigital && station.transfers.length > 0 && (
                      <span className="text-[10px] uppercase text-blue-400 font-mono mt-1">Transfer</span>
                    )}
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </div>
    </div>
  );
};
