import React, { useState } from 'react';
import { Station, LineConfig } from '../types';
import { Plus, Trash2, ArrowUp, ArrowDown, GripVertical } from 'lucide-react';
import { LINE_COLORS } from '../constants';

interface EditorProps {
  stations: Station[];
  config: LineConfig;
  onConfigChange: (newConfig: LineConfig) => void;
  onStationsChange: (newStations: Station[]) => void;
}

export const Editor: React.FC<EditorProps> = ({ stations, config, onConfigChange, onStationsChange }) => {
  const [activeTab, setActiveTab] = useState<'settings' | 'stations'>('settings');

  const handleLineCodeChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    onConfigChange({ ...config, lineCode: e.target.value });
  };

  const updateStation = (id: string, field: keyof Station, value: any) => {
    const updated = stations.map(s => {
      if (s.id === id) {
        if (field === 'transfers' && typeof value === 'string') {
          // Parse comma separated string to array
          return { ...s, transfers: value.split(',').map(t => t.trim()).filter(t => t !== '') };
        }
        return { ...s, [field]: value };
      }
      return s;
    });
    onStationsChange(updated);
  };

  const removeStation = (id: string) => {
    onStationsChange(stations.filter(s => s.id !== id));
  };

  const moveStation = (index: number, direction: 'up' | 'down') => {
    if (direction === 'up' && index === 0) return;
    if (direction === 'down' && index === stations.length - 1) return;

    const newStations = [...stations];
    const swapIndex = direction === 'up' ? index - 1 : index + 1;
    [newStations[index], newStations[swapIndex]] = [newStations[swapIndex], newStations[index]];
    onStationsChange(newStations);
  };

  const addStation = () => {
    const newId = Math.random().toString(36).substr(2, 9);
    onStationsChange([...stations, {
      id: newId,
      name: 'New Station',
      transfers: [],
      isAccessible: false,
      isTerminus: false
    }]);
  };

  return (
    <div className="h-full flex flex-col bg-white border-r border-gray-200 shadow-sm">
      <div className="flex border-b border-gray-200">
        <button
          onClick={() => setActiveTab('settings')}
          className={`flex-1 py-3 font-medium text-sm transition-colors ${activeTab === 'settings' ? 'text-blue-600 border-b-2 border-blue-600' : 'text-gray-500 hover:text-gray-700'}`}
        >
          General Settings
        </button>
        <button
          onClick={() => setActiveTab('stations')}
          className={`flex-1 py-3 font-medium text-sm transition-colors ${activeTab === 'stations' ? 'text-blue-600 border-b-2 border-blue-600' : 'text-gray-500 hover:text-gray-700'}`}
        >
          Stations ({stations.length})
        </button>
      </div>

      <div className="flex-1 overflow-y-auto p-4 custom-scrollbar">
        {activeTab === 'settings' && (
          <div className="space-y-4">
            <div>
              <label className="block text-xs font-semibold text-gray-700 mb-1">Route / Line</label>
              <select
                value={config.lineCode}
                onChange={handleLineCodeChange}
                className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 outline-none"
              >
                {Object.keys(LINE_COLORS).filter(k => k !== 'DEFAULT').map(code => (
                  <option key={code} value={code}>{code} Train</option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-xs font-semibold text-gray-700 mb-1">Theme / Era</label>
              <select
                value={config.theme}
                onChange={(e) => onConfigChange({ ...config, theme: e.target.value as any })}
                className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 outline-none"
              >
                <option value="modern-black">Modern Vinyl (Black)</option>
                <option value="vintage-white">Vintage (White/Silver)</option>
                <option value="digital-find">R160 Digital Display</option>
              </select>
            </div>

            <div>
              <label className="block text-xs font-semibold text-gray-700 mb-1">Via Text (Big Header)</label>
              <input
                type="text"
                value={config.viaText}
                onChange={(e) => onConfigChange({ ...config, viaText: e.target.value })}
                className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 outline-none"
              />
            </div>

            <div>
              <label className="block text-xs font-semibold text-gray-700 mb-1">Direction Text</label>
              <input
                type="text"
                value={config.directionText}
                onChange={(e) => onConfigChange({ ...config, directionText: e.target.value })}
                className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 outline-none"
              />
            </div>

            <div>
              <label className="block text-xs font-semibold text-gray-700 mb-1">Destinations (comma separated)</label>
              <input
                type="text"
                value={config.item_destinations?.join(', ') || ''}
                onChange={(e) => onConfigChange({ ...config, item_destinations: e.target.value.split(',').map(s => s.trim()).filter(s => s !== '') })}
                className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 outline-none"
                placeholder="e.g. Far Rockaway, JFK-AirTrain"
              />
            </div>

            <div>
              <label className="block text-xs font-semibold text-gray-700 mb-1">Boroughs List (comma separated)</label>
              <input
                type="text"
                value={config.item_boroughs?.join(', ') || ''}
                onChange={(e) => onConfigChange({ ...config, item_boroughs: e.target.value.split(',').map(s => s.trim()).filter(s => s !== '') })}
                className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 outline-none"
                placeholder="e.g. Manhattan, Brooklyn, Queens"
              />
            </div>

            <div className="grid grid-cols-2 gap-2">
              <div>
                <label className="block text-xs font-semibold text-gray-700 mb-1">Start Label</label>
                <input
                  type="text"
                  value={config.startText}
                  onChange={(e) => onConfigChange({ ...config, startText: e.target.value })}
                  className="w-full p-2 border border-gray-300 rounded-md text-sm"
                />
              </div>
              <div>
                <label className="block text-xs font-semibold text-gray-700 mb-1">End Label</label>
                <input
                  type="text"
                  value={config.endText}
                  onChange={(e) => onConfigChange({ ...config, endText: e.target.value })}
                  className="w-full p-2 border border-gray-300 rounded-md text-sm"
                />
              </div>
            </div>
          </div>
        )}

        {activeTab === 'stations' && (
          <div className="space-y-3">
            <div className="bg-blue-50 p-3 rounded-md text-xs text-blue-800 mb-4">
              Tip: Use comma separated values for transfers (e.g. "N, Q, R").
            </div>
            {stations.map((station, index) => (
              <div key={station.id} className="bg-white border border-gray-200 rounded-lg p-3 shadow-sm relative group hover:border-blue-400 transition-colors">
                <div className="flex gap-2 mb-2">
                  <div className="flex flex-col gap-1 justify-center">
                    <button onClick={() => moveStation(index, 'up')} disabled={index === 0} className="p-1 text-gray-400 hover:text-blue-600 disabled:opacity-30">
                      <ArrowUp size={14} />
                    </button>
                    <button onClick={() => moveStation(index, 'down')} disabled={index === stations.length - 1} className="p-1 text-gray-400 hover:text-blue-600 disabled:opacity-30">
                      <ArrowDown size={14} />
                    </button>
                  </div>

                  <div className="flex-1 space-y-2">
                    <input
                      type="text"
                      value={station.name}
                      onChange={(e) => updateStation(station.id, 'name', e.target.value)}
                      className="w-full p-1 border-b border-gray-300 focus:border-blue-500 outline-none font-medium text-sm"
                      placeholder="Station Name"
                    />
                    <input
                      type="text"
                      value={station.transfers.join(', ')}
                      onChange={(e) => updateStation(station.id, 'transfers', e.target.value)}
                      className="w-full p-1 border-b border-gray-200 focus:border-blue-500 outline-none text-xs text-gray-600"
                      placeholder="Transfers (e.g. 4,5,6)"
                    />
                  </div>
                </div>

                <div className="flex items-center justify-between mt-2 pt-2 border-t border-gray-100">
                  <div className="flex gap-3">
                    <label className="flex items-center gap-1 cursor-pointer">
                      <input
                        type="checkbox"
                        checked={station.isAccessible}
                        onChange={(e) => updateStation(station.id, 'isAccessible', e.target.checked)}
                        className="rounded text-blue-600 focus:ring-blue-500 h-3 w-3"
                      />
                      <span className="text-xs text-gray-600">ADA</span>
                    </label>
                    <label className="flex items-center gap-1 cursor-pointer">
                      <input
                        type="checkbox"
                        checked={station.isTerminus}
                        onChange={(e) => updateStation(station.id, 'isTerminus', e.target.checked)}
                        className="rounded text-blue-600 focus:ring-blue-500 h-3 w-3"
                      />
                      <span className="text-xs text-gray-600">Terminus</span>
                    </label>
                    <label className="flex items-center gap-1 cursor-pointer">
                      <input
                        type="checkbox"
                        checked={station.isNextStop || false}
                        onChange={(e) => updateStation(station.id, 'isNextStop', e.target.checked)}
                        className="rounded text-blue-600 focus:ring-blue-500 h-3 w-3"
                      />
                      <span className="text-xs text-gray-600">Next Stop</span>
                    </label>
                  </div>
                  <div className="flex-1 ml-4">
                    <input
                      type="text"
                      value={station.borough || ''}
                      onChange={(e) => updateStation(station.id, 'borough', e.target.value)}
                      className="w-full p-1 border-b border-gray-200 outline-none text-xs text-gray-500"
                      placeholder="Borough (for separator)"
                    />
                  </div>
                  <button
                    onClick={() => removeStation(station.id)}
                    className="text-red-400 hover:text-red-600 p-1"
                    title="Remove Station"
                  >
                    <Trash2 size={14} />
                  </button>
                </div>
              </div>
            ))}

            <button
              onClick={addStation}
              className="w-full py-2 border-2 border-dashed border-gray-300 rounded-lg text-gray-500 hover:border-blue-500 hover:text-blue-600 flex items-center justify-center gap-2 font-medium transition-colors"
            >
              <Plus size={16} /> Add Station
            </button>
          </div>
        )}
      </div>
    </div>
  );
};